<template lang="html">
  <div class="page" ref="popWrapper">
    <div class="content">
<a name="process276"></a>

<p><br></p>
<p><span class="rvts17">议事厅（</span><span class="rvts50">The Consulate</span><span class="rvts17">）</span><span class="rvts50">[</span><span class="rvts17">支线</span><span class="rvts50">]</span><span class="rvts17">：</span></p>
<p class="rvps2"><span class="rvts41">坐标：</span>
<br><span class="rvts20">X:205,Y:369</span><span class="rvts21">，地点：着火的蜥蜴理事馆</span>
<br><span class="rvts20"></span>
<br><span class="rvts41">目标：</span>
<br><span class="rvts21">进入理事馆，发现蜥蜴王国的秘密</span>
<br><span class="rvts20"></span>
<br><span class="rvts41">任务详情：</span>
<br><img height="auto" alt="" hspace="1" vspace="1" src="../../../assets/pic2/abcamber1515.jpg" display="block" max-width="100%"><span class="rvts20"></span>
<br><span class="rvts21">到达理事馆后，会发现这里一片诅咒火的海洋。由于诅咒火焰中充满了不死的蜥蜴怪，因此想要近一步调查非常困难。</span>
<br><span class="rvts21">在火焰中守卫的不死族蜥蜴，会在你杀掉它后瞬间复活。唯一可以让它们彻底死掉的方法是把理事馆区域的所有诅咒火焰都扑灭（使用祝福或水系技能</span><span class="rvts20"></span><span class="rvts21">龙卷风</span><span class="rvts20"></span><span class="rvts21">）</span>
<br><span class="rvts21">更简单的方法是找到队伍中</span><span class="rvts20"></span><span class="rvts21">潜行</span><span class="rvts20"></span><span class="rvts21">和</span><span class="rvts20"></span><span class="rvts21">偷窃</span><span class="rvts20"></span><span class="rvts21">技能最强的盗贼，通过图中的这个</span><span class="rvts20">BUG</span><span class="rvts21">区域跳跃过去，并一路潜行加开锁，几十秒的时间就可以溜进理事馆核心区域。</span>
<br><span class="rvts20"></span>
<br><img height="auto" alt="" hspace="1" vspace="1" src="../../../assets/pic2/abcamber1516.jpg" display="block" max-width="100%"><span class="rvts20"></span>
<br><span class="rvts21">有两个并排的大门，通向理事馆花园区，需要使用钥匙（钥匙在理事馆中的一处办公室的死亡蜥蜴身上，但到达那里需要面对</span><span class="rvts20">3</span><span class="rvts21">个不死族蜥蜴），或者使用开锁技能。</span>
<br><span class="rvts20"></span>
<br><img height="auto" alt="" hspace="1" vspace="1" src="../../../assets/pic2/abcamber1517.jpg" display="block" max-width="100%"><span class="rvts20"></span>
<br><span class="rvts21">到达理事馆后花园，使用</span><span class="rvts20"></span><span class="rvts21">源力视野</span><span class="rvts20"></span><span class="rvts21">，可以找到隐形的一扇传送门。</span>
<br><span class="rvts20"></span>
<br><img height="auto" alt="" hspace="1" vspace="1" src="../../../assets/pic2/abcamber1518.jpg" display="block" max-width="100%"><span class="rvts20"></span>
<br><span class="rvts21">进入传送门后，瞬间来到一个奇异的竞技场。此时如果有任何一名队友离开圆圈区域，将会遭遇亚历山大的幻想，一场艰难的打斗一触即发。</span>
<br><span class="rvts21">加入这场战斗的都是《神界：原罪</span><span class="rvts20">2</span><span class="rvts21">》中的实力派敌人：亚历山大，温蒂格，马拉迪以及后来被温蒂格召唤出来的邪恶蜥蜴王子。</span>
<br><span class="rvts21">如果你感觉这场打斗实在是难度太大，可以尝试下列方法。</span>
<br><span class="rvts20"></span>
<br><img height="auto" alt="" hspace="1" vspace="1" src="../../../assets/pic2/abcamber1519.jpg" display="block" max-width="100%"><span class="rvts20"></span>
<br><span class="rvts21">使用队伍中学了召唤法术的角色，召唤出化身，让它进入竞技场，把所有的镜子都打碎，记得一定要留一面镜子，这面镜子用于温蒂格召唤出邪恶蜥蜴王子，触发额外的剧情。</span>
<br><span class="rvts20"></span>
<br><img height="auto" alt="" hspace="1" vspace="1" src="../../../assets/pic2/abcamber1520.jpg" display="block" max-width="100%"><span class="rvts20"></span>
<br><span class="rvts21">打斗开始，如果你按照以上方法，击碎了所有镜子，只留了一面，那么可以非常容易通过打斗。如果所有的镜子都还存在，那么亚历山大，温蒂格，马拉迪和邪恶蜥蜴王子会无限从镜子中复活下去。</span>
<br><span class="rvts21">因此这场战斗的取胜关键就是首先要打碎所有的镜子。</span>
<br><span class="rvts20"></span>
<br><img height="auto" alt="" hspace="1" vspace="1" src="../../../assets/pic2/abcamber1521.jpg" display="block" max-width="100%"><span class="rvts20"></span>
<br><span class="rvts21">打败他们后，可以进入竞技场中的隐藏闪光大门。</span>
<br><span class="rvts20"></span>
<br><img height="auto" alt="" hspace="1" vspace="1" src="../../../assets/pic2/abcamber1522.jpg" display="block" max-width="100%"><span class="rvts20"></span>
<br><span class="rvts21">之后我们被传送到古代蜥蜴王国，这里面正在举行着各种古怪的仪式。</span>
<br><span class="rvts20"></span>
<br><img height="auto" alt="" hspace="1" vspace="1" src="../../../assets/pic2/abcamber1523.jpg" display="block" max-width="100%"><span class="rvts20"></span>
<br><span class="rvts21">尝试说服举行仪式的萨满，他会让你顺利通过并让你拯救蜥蜴公主。</span>
<br><span class="rvts21">如果该场景没有成功说服蜥蜴萨满，将会迎来一场战斗，四大元素萨满会在刚开始占据高地优势，对我们进行夹击。</span>
<br><span class="rvts21">这场战斗的取胜关键在于你需要了解每个萨满的元素弱点，不要使用他们元素派别（因为当抗性高于</span><span class="rvts20">100%</span><span class="rvts21">，反而会使他们恢复生命），并打败他们。</span>
<br><span class="rvts20"></span>
<br><img height="auto" alt="" hspace="1" vspace="1" src="../../../assets/pic2/abcamber1524.jpg" display="block" max-width="100%"><span class="rvts20"></span>
<br><span class="rvts21">来到蜥蜴公主这里，如果队伍中没有猩红王子，那么所有的桥段都指向一场与公主的战斗（无论你是否使用蜥蜴变脸）。</span>
<br><span class="rvts21">但是如果队伍中有蜥蜴王子，会多出额外的剧情。</span>
<br><span class="rvts21">首先，在了解到蜥蜴公主也已经与神王结下誓约，那么如果队伍中有角色身上背有</span><span class="rvts20"></span><span class="rvts21">破誓者</span><span class="rvts20"></span><span class="rvts21">，那么蜥蜴公主会找你要这个道具，如果你拒绝给她，会降低猩红王子的态度值。接着，无论你是否给了她破誓者，</span>
<br><span class="rvts21">剧情</span><span class="rvts20">1</span><span class="rvts21">：让蜥蜴公主和蜥蜴王子孵化龙蛋，那么他们都会站在你这一边，并在最后，猩红王子可以选择带走一颗龙蛋，这会使得猩红王子获得</span><span class="rvts20"></span><span class="rvts21">召唤蛟龙</span><span class="rvts20"></span><span class="rvts21">技能。</span>
<br><span class="rvts21">剧情</span><span class="rvts20">2</span><span class="rvts21">：如果你告诉蜥蜴王子需要杀掉他和蜥蜴公主孵化的龙宝宝，那么他们两个都会与你敌对起来，并且此时蜥蜴公主的刺客保镖都会瞬间登场，一场混战之后，再也无法使蜥蜴王子回归队伍。</span>
<br><span class="rvts20"></span>
<br><span class="rvts20"></span>
<br><img height="auto" alt="" hspace="1" vspace="1" src="../../../assets/pic2/abcamber1525.jpg" display="block" max-width="100%"><span class="rvts39"></span>
<br><span class="rvts19">另外，如果队伍中有蜥蜴王子，并且之前在死神海岸的蜥蜴商队与公主亲密过，那么公主会浮出一只龙蛋，蜥蜴王子可以带上龙蛋，随时召唤它为蜥蜴王子作战。</span></p>
<p><br></p>
<p><br></p>

    </div>
  </div>
</template>
<script>
import BScroll from 'better-scroll'
export default {
  data () {
    return {};
  },
  methods: {
    _initScroll() {
      this.popScroll = new BScroll(this.$refs.popWrapper, {
        click: true,
        probeType: 3
      });
    }
  },
  created() {
    this.$nextTick(() => {
      this._initScroll();
    });
  }
};
</script>
<style>
@import '../../../../static/css/common.css';
</style>



